article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
nav,
section,
summary {
  display: block;
}
audio,
canvas,
video {
  display: inline-block;
}
audio:not([controls]) {
  display: none;
  height: 0;
}
[hidden] {
  display: none;
}
html {
  font-family: sans-serif;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}
a:focus {
  outline: thin dotted;
}
a:active,
a:hover {
  outline: 0;
}
h1 {
  font-size: 2em;
}
abbr[title] {
  border-bottom: 1px dotted;
}
b,
strong {
  font-weight: 700;
}
dfn {
  font-style: italic;
}
mark {
  background: #ff0;
  color: #000;
}
code,
kbd,
pre,
samp {
  font-family: monospace, serif;
  font-size: 1em;
}
pre {
  white-space: pre-wrap;
  word-wrap: break-word;
}
q {
  quotes: \201c \201d \2018 \2019;
}
small {
  font-size: 80%;
}
sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}
sup {
  top: -0.5em;
}
sub {
  bottom: -0.25em;
}
img {
  border: 0;
}
svg:not(:root) {
  overflow: hidden;
}
fieldset {
  border: 1px solid silver;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em;
}
button,
input,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  margin: 0;
}
button,
input {
  line-height: normal;
}
button,html input[type=button],/* 1 */input[type=reset],input[type=submit] {
  -webkit-appearance: button;
  cursor: pointer;
}
button[disabled],
input[disabled] {
  cursor: default;
}
input[type="checkbox"],
input[type="radio"] {
  box-sizing: border-box;
  padding: 0;
}
input[type="search"] {
  -webkit-appearance: textfield;
  -moz-box-sizing: content-box;
  -webkit-box-sizing: content-box;
  box-sizing: content-box;
}
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}
textarea {
  overflow: auto;
  vertical-align: top;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
body,
figure {
  margin: 0;
}
legend,
button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}

.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: "";
  clear: both;
  height: 0;
}

* {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

body {
  font-family: "Raleway", sans-serif;
  line-height: 0.8;
  background-color: gainsboro;
  color: grey;
}

.studentContainer {
  background-color: white;
  width: 800px;
  min-height: 400px;
  max-height: 600px;
  overflow-x: hidden;
  overflow-y: scroll;
  margin: 50px auto;
  border-radius: 15px;
  box-shadow: grey 0px 4px 4px;
  position: relative;
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}

.studentContainer::-webkit-scrollbar {
  display: none;
}

.searchForm {
  width: 100%;
  text-align: center;
}

input {
  border: 0;
  border-bottom: solid 1px gainsboro;
  font-size: 1.5rem;
  font-family: "Raleway", sans-serif;
}

.searchForm input {
  width: 95%;
  padding: 15px 0;
  margin: 0 auto;
}

input:focus {
  outline: none;
  border-bottom: solid 1px black;
}

input::placeholder {
  color: grey;
}

.student {
  border-bottom: solid 1px gainsboro;
  display: flex;
  padding-left: 30px;
}

.student:last-child {
  border-bottom: solid 1px transparent;
}

.student .robotAvatar {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  border: solid 1px grey;
  margin-right: 20px;
  margin-top: 15px;
}

.studentName {
  text-transform: uppercase;
  color: black;
  font-size: 1.5rem;
  font-weight: 700;
}

.infoIndented {
  padding-left: 25px;
}

.buttonHolder {
  position: absolute;
  right: 15px;
  padding-top: 15px;
}

.buttonHolder button {
  border: 0;
  background-color: inherit;
}

.buttonHolder button:focus {
  outline: none;
}

.buttonHolder i {
  font-size: 1.5rem;
  color: grey;
}

.grade {
  padding-left: 15px;
}

.gradeList {
  line-height: 0.2;
  padding-top: px;
}

.tagButton {
  border: 0;
  border-radius: 5px;
  background-color: gainsboro;
  padding: 10px 20px;
}

.tagForm {
  width: 200px;
  padding-top: 0;
  padding-bottom: 15px;
}

.tagForm input {
  width: 100%;
  padding: 7px 0px;
  font-size: 1rem;
}

.tag {
  color: black;
  background-color: gainsboro;
  border-radius: 7px;
  display: inline-block;
  margin-right: 10px;
  padding: 10px;
}
